import React from 'react';
import { Main, Box, Image, Button, Nav, Header, Card, CardHeader, CardBody, Select, Text, TextInput, } from 'grommet';
import background from '../../assets/background_home.png'
import Avatar from '@mui/material/Avatar';
import Stack from '@mui/material/Stack';
import { styled } from '@mui/material/styles';
import MyLocationIcon from '@mui/icons-material/MyLocation';
import LocationOnIcon from '@mui/icons-material/LocationOn';
import backmid from '../../assets/backgroundmiddle.jpg'
import logobus from '../../assets/logobusticket.png';
import QueryBuilderIcon from '@mui/icons-material/QueryBuilder';
import { Link } from "react-router-dom";
import api from '../../services/api';
import Popover from '@mui/material/Popover';
import KeyboardArrowDownIcon from '@mui/icons-material/KeyboardArrowDown';
import Dialog from '@mui/material/Dialog';
import DialogContent from '@mui/material/DialogContent';
import DialogContentText from '@mui/material/DialogContentText';
import DialogTitle from '@mui/material/DialogTitle';

export const HomeScreen = ({ handleChange }) => {

    const BoxHome = [
        {
            color: 'transparent',
            size: "15vh",
            style: "solid",
            side: "left"
        },
        {
            color: 'transparent',
            size: "15vh",
            style: "solid",
            side: "right"
        },
        {
            color: 'transparent',
            size: "5vh",
            // style: "solid",
            side: "top"
        }
    ];

    const styleBackGroundHome = {
        backgroundImage: `url(${background})`,
        backgroundRepeat: 'no-repeat',
        backgroundSize: 'cover',
        backgroundPosition: 'center'

    }

    const styles = {
        center: {
            display: 'flex', justifyContent: 'center', alignItems: 'center', backgroundImage: `url(${backmid})`
        },
        centerLogo: {
            display: 'flex', justifyContent: 'center', alignItems: 'center'
        },
        prefooter: {
            display: 'flex', justifyContent: 'center', alignItems: 'center'
        },
        buttonTicket: {
            width: '13vh', height: '8vh', backgroundColor: '#339aca', color: 'white', borderColor: 'transparent', borderRadius: '7%', textAlign: 'center'
        },
        buttonTicketCancelar: {
            width: '12vh', height: '8vh', backgroundColor: 'gray', color: 'white', borderColor: 'transparent', borderRadius: '7%', textAlign: 'center'
        },
        buttonTicketQuantidade: {
            height: '4.5vh', width: '40%', backgroundColor: '#339aca', color: 'black', borderColor: 'black', border: '2px', borderRadius: '7%', textAlign: 'center'
        }
    }

    const [origem, setOrigem] = React.useState('Toncontin')
    const [destino, setDestino] = React.useState('Palmerola')
    const [quantidade, setQuantidade] = React.useState(1);
    const [valor, setValor] = React.useState(200);
    const [horarioIda, setHorarioida] = React.useState('07:00')
    const [horarioChegada, setHorarioChegada] = React.useState('08:30')
    const [nome, setNome] = React.useState('')
    const [sobrenome, setSobrenome] = React.useState('')
    const [documento, setDocumento] = React.useState('')
    const [user, setUser] = React.useState([])
    const [loading, setLoading] = React.useState(true)
    const [quantidadeVendas, setQtdVendas] = React.useState(0)
    const [valorVendas, setVlrVendas] = React.useState(0)
    const horarioReduzidos = ['07:00', '08:30', '14:30', '16:00'];
    const [pagando, setPagando] = React.useState(false);
    const horarios = [
        '06:00',
        '06:30',
        '07:00',
        '07:30',
        '08:00',
        '08:30',
        '09:00',
        '09:30',
        '10:00',
        '10:30',
        '11:00',
        '11:30',
        '12:00',
        '12:30',
        '13:00',
        '13:30',
        '14:00',
        '14:30',
        '15:00',
        '15:30',
        '16:00',
        '16:30',
        '17:00',
        '17:30',
        '18:00',
        '18:30',
        '19:00',
        '19:30',
        '20:00',
        '20:30',
        '21:00',
        '21:30',
        '22:00',
        '22:30',
        '23:00',
        '23:30',
        '00:00',
        '00:30'
    ];

    const handleCancelar = () => {
        setNome('');
        setSobrenome('');
        setDocumento('');
        setQuantidade(1);
        setPagando(false)
    }

    React.useEffect(() => {
        let token = localStorage.getItem("token");

        let config = {
            headers: {
                Authorization: 'Bearer ' + token
            }
        }

        let data = {

        }

        api.post('/me', data, config).then((response) => {
            if (response.data.message === 'sucess') {
                console.log(response.data);
                setUser(response.data.usuario.original)
                setLoading(false)
                return 1;
            }
            else {
                localStorage.clear();

            }

        })
            .catch((error) => console.log(error))
    }, [])

    function stringToColor(string) {
        let hash = 0;
        let i;

        /* eslint-disable no-bitwise */
        for (i = 0; i < string.length; i += 1) {
            hash = string.charCodeAt(i) + ((hash << 5) - hash);
        }

        let color = '#';

        for (i = 0; i < 3; i += 1) {
            const value = (hash >> (i * 8)) & 0xff;
            color += `00${value.toString(16)}`.substr(-2);
        }
        /* eslint-enable no-bitwise */

        return color;
    }

    function stringAvatar(name) {
        return {
            sx: {
                bgcolor: stringToColor(name),
            },
            children: `${name.split(' ')[0][0]}${name.split(' ')[1][0]}`,
        };
    }

    const [anchorEl, setAnchorEl] = React.useState(null);

    const handleClick = (event) => {
        setAnchorEl(event.currentTarget);
    };

    const handleClose = () => {
        setAnchorEl(null);
    };

    const open = Boolean(anchorEl);
    const id = open ? 'simple-popover' : undefined;


    const handlePagamento = (forma_pago) => {
        console.log(origem, destino, quantidade, valor, nome, sobrenome, documento);
        var data = new Date();
        var dia = String(data.getDate()).padStart(2, '0');
        var mes = String(data.getMonth() + 1).padStart(2, '0');
        var ano = String(data.getFullYear().toString().substr(-2));

        console.log(ano);
        if (origem === 'Toncontin') {
            var punto_emission = '002 de Toncontin a Palmerola';
            var correlativo_autorizado_desde = '001-002-01-00000001';
            var correlativo_autorizado_hasta = '001-002-01-00020000';
            var factura_numero = '001-002-01-00000098'

        }
        else {
            var punto_emission = '003 de Palmerola a Toncontin';
            var correlativo_autorizado_desde = '001-003-01-00000001';
            var correlativo_autorizado_hasta = '001-003-01-00020000';
            var factura_numero = '001-003-01-00000058';
        }

        var fecha = dia + '/' + mes + '/' + ano;
        var hora = data.getHours() + ":" + data.getMinutes()
        var vendedor = user.nome;
        var cliente = nome;
        var doc = documento;
        var precio = valor;
        var descuento = 0;
        var importe_gravado = 0;
        var importe_exento = valor;
        var isv = 0;
        var total = valor;
        let user_id = user.id;



        api.post('ticket-create', {
            punto_emission: punto_emission,
            correlativo_autorizado_desde: correlativo_autorizado_desde,
            correlativo_autorizado_hasta: correlativo_autorizado_hasta,
            factura_numero: factura_numero,
            fecha: fecha,
            hora: hora,
            vendedor: vendedor,
            cliente: cliente,
            documento: doc,
            precio: precio,
            descuento: descuento,
            importe_gravado: importe_gravado,
            importe_exento: importe_exento,
            isv: isv,
            total: total,
            user_id: user_id,
            forma_pago: forma_pago,
            cantidad: quantidade,
            condutor: 'vitor',
            origen: origem,
            destino: destino,
        }).then((response) => {
            console.log(response.data)
            window.open("http://142.93.54.7/api/ticket-get/" + response.data.id, "_blank");
            setQtdVendas(quantidadeVendas + quantidade);
            setVlrVendas(valorVendas + valor);

            setPagando(false)
            setQuantidade(1);
            setNome('');
            setSobrenome('');
            setDocumento('');
            setValor(200)
            setOpen(false);
        })




    }
    const [openModal, setOpen] = React.useState(false);


    return (
        < Main>
            <Dialog
                open={openModal}
                onClose={() => setOpen(false)}
                aria-labelledby="alert-dialog-title"
                aria-describedby="alert-dialog-description"
            >
                <DialogTitle id="alert-dialog-title">
                    <p style={{ textAlign: 'center' }}> Seleccione la forma de pago para emitir el ticket</p>
                </DialogTitle>
                <DialogContent>
                    <DialogContentText id="alert-dialog-description">
                        <Stack direction={'row'} spacing={5} alignContent={'center'} display={'flex'} justifyContent={'center'}>
                            <Button primary style={styles.buttonTicket} onClick={() => { handlePagamento('TARJETA') }} >
                                <Stack direction={'column'} >
                                    <Text > TARJETA </Text>

                                </Stack>
                            </Button>
                            <Button primary style={styles.buttonTicket} onClick={() => { handlePagamento('EFECTIVO') }} >
                                <Stack direction={'column'} >
                                    <Text > EFECTIVO</Text>
                                </Stack>
                            </Button>
                        </Stack>
                    </DialogContentText>
                </DialogContent>
            </Dialog>
            {loading ? (<> </>) : (<Box style={styleBackGroundHome}>
                <Box border={BoxHome} pad="medium">
                    <Header pad="small">
                        <Image fill={false} style={{ width: '50%' }} src={logobus} />

                        <Stack direction={'row'} spacing={2} alignItems={'center'}>

                            <Avatar {...stringAvatar(user.nome)} />

                            <Button onClick={handleClick}>
                                <KeyboardArrowDownIcon />
                            </Button>
                            <Popover
                                id={id}
                                open={open}
                                anchorEl={anchorEl}
                                onClose={handleClose}
                                anchorOrigin={{
                                    vertical: 'bottom',
                                    horizontal: 'left',
                                }}

                            >
                                <Stack spacing={2} >
                                    <Text style={{ textAlign: 'center' }}> Ventas </Text>
                                    <Text> - #{quantidadeVendas} </Text>
                                    <Text> - L. {valorVendas}.00 </Text>
                                    <Button primary style={{ height: '4.5vh', backgroundColor: 'white', color: 'black', borderColor: 'transparent' }} label={'Cerrar sesión'} onClick={() => { localStorage.clear(); handleChange(); }} />
                                </Stack>
                            </Popover>

                        </Stack>
                    </Header>

                    {pagando ? (
                        <>

                            <Card height="small" width="fill" background="light-1" alignContent='center' alignItems='center' style={{ marginTop: '15vh', marginBottom: '45vh' }}  >
                                <CardHeader>

                                </CardHeader>
                                <CardBody pad="medium" direction='row' gap='large' style={{ marginTop: '3%', position: 'relative' }}>
                                    <Stack direction={'column'} width={'20%'}>
                                        <Text size='medium'>Nombre</Text>
                                        <TextInput
                                            // placeholder="type here"
                                            value={nome}
                                            onChange={event => setNome(event.target.value)}
                                            size={'medium'}
                                        />
                                    </Stack>

                                    <Stack direction={'column'} width={'20%'}>
                                        <Text size='medium'>Apellido</Text>
                                        <TextInput
                                            // placeholder="type here"
                                            value={sobrenome}
                                            onChange={event => setSobrenome(event.target.value)}
                                            size={'medium'}
                                        />
                                    </Stack>

                                    <Stack direction={'column'} width={'20%'}>
                                        <Text size='medium'>RTN / ID</Text>

                                        <TextInput
                                            // placeholder="type here"
                                            value={documento}
                                            onChange={event => setDocumento(event.target.value)}
                                            size={'medium'}
                                        />
                                    </Stack>

                                    <Button primary style={styles.buttonTicketCancelar} onClick={() => handleCancelar()} >
                                        <Stack direction={'column'} >
                                            <Text > Cancelar </Text>
                                        </Stack>
                                    </Button>

                                    <Button primary style={styles.buttonTicket} onClick={() => { setOpen(true) }} >
                                        <Stack direction={'column'} >
                                            <Text > Siguiente </Text>
                                            <Text >L. {valor}.00 </Text>
                                        </Stack>
                                    </Button>

                                </CardBody>
                            </Card>
                        </>
                    ) : (
                        <>
                            <Card height="small" width="fill" background="light-1" alignContent='center' alignItems='center' style={{ marginTop: '15vh', marginBottom: '45vh' }}  >
                                <CardHeader>

                                </CardHeader>
                                <CardBody pad="medium" direction='row' gap='large' style={{ marginTop: '3%', position: 'relative' }}>
                                    <Stack direction={'column'} width={'20%'}>
                                        <Text size='medium'>Origen</Text>
                                        <Select
                                            icon={() => (<MyLocationIcon style={{ color: '#fc8332' }} />)}
                                            options={['Toncontin', 'Palmerola']}
                                            value={origem}
                                            onChange={({ option }) => { setOrigem(destino); setDestino(origem); }}
                                            size={'small'}
                                        />
                                    </Stack>

                                    <Stack direction={'column'} width={'20%'}>
                                        <Text size='medium'>Destino</Text>
                                        <Select
                                            icon={() => (<LocationOnIcon style={{ color: '#df5a5e' }} />)}
                                            options={['Toncontin', 'Palmerola']}
                                            value={destino}
                                            onChange={({ option }) => { setOrigem(destino); setDestino(origem); }}
                                            size={'small'}
                                        />
                                    </Stack>

                                    <Stack direction={'column'} width={'20%'} style={{ marginTop: '-2vh' }}>
                                        <Text size='medium'>Horario</Text>
                                        <Stack direction={'column'} width={'100%'}>



                                            <Select
                                                icon={() => (<QueryBuilderIcon style={{ color: '#df5a5e' }} />)}
                                                options={horarioReduzidos}
                                                value={origem == 'Toncontin' ? ('07:00') : ('14:30')}

                                                // options={horarios}
                                                // value={horarioIda}
                                                // onChange={({ option }) => { setHorarioida(option) }}
                                                size={'small'}
                                            />

                                            <Select
                                                icon={() => (<QueryBuilderIcon style={{ color: '#df5a5e' }} />)}

                                                options={horarioReduzidos}
                                                value={origem == 'Toncontin' ? ('08:30') : ('16:00')}

                                                // options={horarios}
                                                // value={horarioChegada}
                                                // onChange={({ option }) => { setHorarioChegada(option) }}
                                                size={'small'}
                                            />
                                        </Stack>
                                    </Stack>

                                    <Stack direction={'column'} width={'20%'}>
                                        <Text size='medium'>Cantidad</Text>

                                        <Stack direction={'row'} spacing={4}>
                                            <Button primary style={styles.buttonTicketQuantidade} onClick={() => { if ((quantidade - 1) > 0) { setQuantidade(quantidade - 1); setValor((quantidade - 1) * 200) } }}>
                                                <Text style={{ textAlign: 'center' }}> -</Text>

                                            </Button>
                                            <Button primary style={styles.buttonTicketQuantidade} >
                                                <Text> {quantidade} </Text>

                                            </Button>
                                            <Button primary style={styles.buttonTicketQuantidade} onClick={() => { setQuantidade(quantidade + 1); setValor((quantidade + 1) * 200) }}>
                                                <Text> + </Text>

                                            </Button>
                                        </Stack>
                                    </Stack>

                                    <Stack direction={'column'} width={'20%'}>
                                        <Text size='medium' >Total</Text>

                                        <Stack direction={'row'} spacing={4}>
                                            <Text size='xxlarge'> L. {valor}.00</Text>

                                        </Stack>
                                    </Stack>

                                    <Button primary style={styles.buttonTicket} onClick={() => { setPagando(true) }} >
                                        <Text > Siguiente </Text>

                                    </Button>

                                </CardBody>
                            </Card>
                        </>
                    )}

                </Box>

            </Box>)}


        </Main >
    )
};

export default HomeScreen;